@import url(https://fonts.googleapis.com/css2?family=Ruda:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
}

@media (max-width: 1200px ) {
  .wrapper {
    max-width: 900px;
  }
}

@media (max-width: 992px ) {
  .wrapper {
    max-width: 750px;
  }
}

@media (max-width: 500px ) {
  .wrapper {
    max-width: 480px;
  }
}


.header {
  max-width: 100%;
  position: relative;
  width: 100%;
  height: 70px;
  background: linear-gradient(to bottom, #2B3A67 71%, #66999B 71%);
}

.header-wrapper {
max-width: 1000px;
}

.logo-wrapper {
  position: absolute;
  top: 35.5%;
  left: 18%;
}

.logo {
  width: 50px;
  height: 50px;
  background-color: #FFC482;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.logo-letter {
  display: inline-block;
  font-family: Ruda, sans-serif;
  font-size: 36px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #ffffff;
}

.logo-letter .up {  
  display: inline-block;
  position: absolute;
  top: -14%;
  right: 10%;
  color: #66999B;
}

.logo-letter .down {
  display: inline-block;
  position: absolute;
  top: 24%;
  left: 10%;
  color: #ffffff;

}

@media (max-width: 1400px) {
  .logo-wrapper { 
  margin-left: 2%;
  left: 3%;
  }
}

.popup_content {
  width: 45%;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);  
  padding: 1%;
  margin-top: 20px;
  padding-top: 3%;
  max-height: 100%;
}

.popup_header {
  display: flex;
  align-items: center;
  padding-bottom: 5%;
  justify-content: space-around
}

.popup_header h3 {
  font-family: Roboto, sans-serif;
  font-size: 64px;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
}

.toggle-wrapper p {
  font-family: Ruda, sans-serif;
  font-size: 24px;
  padding-right: 15px;
}



.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2B3A67;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked+.slider {
  background-color: #ccc;
}

input:focus+.slider {
  box-shadow: 0 0 1px #ccc;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}



/*popup*/
.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0,0,0,0.4);
  position: fixed;
  top:0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
 
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.overlay {
  background: rgba(0, 0, 0, 0);
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
}

.group {
  margin: 10px;
  padding: 5px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: bold;
  position: relative;
}

.group label {
  padding-left: 10px;

}

.group input[type="text"] {
  margin-top: 10px;
  height: 47px;
  width: 467px;
  background: #FFFFFF;
  border: 1px solid #2B3A67;
  box-sizing: border-box;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 0px 10px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: bold;
  padding-left: 15px;
}

.group-input {
  display: block;
}

.form-validation {
  display: inline-block;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  color: #FF0000;
  position: absolute;
  top: 50%;
  left: 60%;

}


/*image*/

.group.file {
  display: flex;
  padding-left: 0;
  margin-left: 0;
}

label .group-input {
  padding-left: 0;
}

.group.file button {
  height: 47px;
}

.group-input.file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.input-file {
  display: inline-block;
  width: 276px;
  height: 47px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  background: #FFFFFF;
  border: 1px solid #EEEEEE;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 0px 10px;

  line-height: 47px;
  text-align: center;
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 83px;
  height: 83px;
  border-radius: 50%;
  background-color: #60DABF;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.avatar {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 56px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.form-buttons {
  display: flex;
  justify-content: space-between;
  padding: 85px 15px 15px;
}

.button.button-blue,
.button.button-white {
  width: 189px;
  height: 53px;
  font-family: Ruda;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  border: 1px solid #496A81;
  border-radius: 3px;
}

.button.button-blue {
  color: #FFFFFF;
}

.button.button-white {
  height: 60px;
  background-color: #FFFFFF;
