.welcome-wrapper {
  padding-top: 8%;
  height: 750px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-left: 15%;
}

.main-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 10%;
  align-content: center;
  margin: 0 auto;
  padding-top: 8%;
  padding-bottom: 70px;
  width: 50%;
}

.game-title {
  grid-column: 1/3;
  grid-row: 1/3;
  align-self: end;
  position: relative;
  left: 30%;
}

.title {
  font-family: Roboto, sans-serif;
  font-size: 70px;
  font-weight: bold;
}

.heading {
  font-family: Roboto, sans-serif;
  font-size: 48px;
  font-weight: bold;
  color: #66999B;
  grid-column: 1/3;
  justify-self: center;
  align-self: center;
}

.form-text {
  grid-column: 1/3;
  justify-self: center;
  align-self: center;
  text-transform: uppercase;
}

.form-input {
  font-family: Roboto, sans-serif;
  font-size: 30px;
  line-height: 30px;
}

.form-input.up {
  grid-column: 1/3;
  justify-self: start;
  align-self: start;
}

.button {
  font-family: Ruda;
  font-weight: bold;
  font-size: 24px;
  width: 241px;
  height: 38px;
  color: white;
  background-color: #2B3A67;
  border-radius: 2px;
}

.button:hover,
.button:active {
  cursor: pointer;
  background-color: #496A81;
}

.colored {
  color: #66999B;
}

.title-line {
  width: 350px;
  border: 5px solid #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-transform: rotate(164.77deg);
          transform: rotate(164.77deg);
  position: absolute;
  top: 50%;
  left: 33%;
}

.title1,
.title2 {
  position: absolute;
  display: inline-block;
}

.title1 {
  left: 26%
}

.title2 {
  top: 50%;
  left: 55%;
}

@media (max-width: 1400px) {
  .welcome-wrapper {
    margin-left: 2%;
  }

  .title-line {
    width: 330px;
  }
}


@media (max-width: 995px) {
  .welcome-wrapper {
    padding-top: 10%
  }

  .game-title {
    left: 5%;
  }

  .title-line {
    width: 300px;
  }
}

@media (max-width: 767px) {
  .welcome-wrapper {
