
.popup_content {
  width: 45%;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);  
  padding: 1%;
  margin-top: 20px;
  padding-top: 3%;
  max-height: 100%;
}

.popup_header {
  display: flex;
  align-items: center;
  padding-bottom: 5%;
  justify-content: space-around
}

.popup_header h3 {
  font-family: Roboto, sans-serif;
  font-size: 64px;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
}

.toggle-wrapper p {
  font-family: Ruda, sans-serif;
  font-size: 24px;
  padding-right: 15px;
}



.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2B3A67;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked+.slider {
  background-color: #ccc;
}

input:focus+.slider {
  box-shadow: 0 0 1px #ccc;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}



/*popup*/
.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0,0,0,0.4);
  position: fixed;
  top:0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
 
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}
